import * as React from 'react';
import { IntlProvider as Provider } from 'react-intl';
import moment from 'moment';
import { Sprkkode } from './types';
import nnMessages from './messages/nn_NO.json';
import nbMessages from './messages/nb_NO.json';
import enMessages from './messages/en_US.json';
import { allCommonMessages } from '@navikt/fp-common';

interface Props {
    sprkkode: Sprkkode;
    children: React.ReactNode;
}

moment.locale('nb');

const getLanguageMessages = (sprkkode: Sprkkode) => {
    if (sprkkode === 'nb') {
        return { ...nbMessages, ...allCommonMessages.nb };
    } else if (sprkkode === 'nn') {
        return { ...nnMessages, ...allCommonMessages.nn };
    } else {
        return { ...enMessages, ...allCommonMessages.en };
    }
};

const IntlProvider: React.FunctionComponent<Props> = ({ sprkkode, children }) => {
    return (
        <Provider locale={sprkkode} messages={getLanguageMessages(sprkkode) || {}}>
            {children}
        </Provider>
    );
};
export default IntlProvider;
